img {
  height: auto;
  display: block;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

p {
  margin: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Poppinsregular.woff2") format("woff2"), url("../fonts/Poppinsregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/Poppinspoppins-semiboldmedium.woff2") format("woff2"), url("../fonts/Poppinspoppins-semiboldmedium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Poppinsbold.woff2") format("woff2"), url("../fonts/Poppinsbold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Roboto.woff2") format("woff2"), url("../fonts/Roboto.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/Robotomedium.woff2") format("woff2"), url("../fonts/Robotomedium.woff") format("woff");
  font-display: swap;
}
@media (min-width: 320px) {
  .br--mobile {
    display: block;
  }
  .br--tablet, .br--desktop {
    display: none;
  }
}
@media (min-width: 768px) {
  .br--tablet {
    display: block;
  }
  .br--mobile, .br--desktop {
    display: none;
  }
}

.page {
  height: 100%;
  font-family: "Poppins", "Arial", "Helvetica", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
}

.page__body {
  margin: 0;
  background-color: #ecf7f3;
  color: #000000;
  display: flex;
  flex-direction: column;
  min-height: 100%;
}
@media (min-width: 1400px) {
  .page__body {
    padding: 30px 141px 44px 149px;
    max-width: 1400px;
    margin: 0 auto;
  }
}

.logo {
  display: block;
}

.logo__image {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .logo__image {
    margin: 0;
  }
}

.page-header {
  position: relative;
  text-align: center;
  padding: 20px 0;
}
@media (min-width: 768px) {
  .page-header {
    display: grid;
    grid-template-columns: max-content 1fr max-content;
    align-items: center;
    margin-bottom: 119px;
    padding: 31px 60px 0 60px;
  }
}
@media (min-width: 1400px) {
  .page-header {
    padding: 0;
  }
}

.page-nav__wrapper {
  display: none;
  position: absolute;
  top: 0;
  width: 100%;
}
@media (min-width: 768px) {
  .page-nav__wrapper {
    position: relative;
    display: grid;
    grid-template-columns: 1fr max-content;
    align-items: center;
  }
}

.page-nav__login {
  display: block;
  padding: 20px;
  font-weight: 400;
  line-height: 24px;
  color: #ffffff;
  text-transform: uppercase;
  background-color: #a2a2a2;
}
.page-nav__login:hover {
  background-color: #48b774;
}
.page-nav__login:active {
  background-color: #ff8345;
}
@media (min-width: 768px) {
  .page-nav__login:hover {
    background-color: #48b774;
    opacity: 0.8;
  }
  .page-nav__login:active {
    background-color: #ff8345;
    opacity: 1;
  }
}
@media (min-width: 768px) {
  .page-nav__login {
    font-weight: 500;
    color: #ffffff;
    background-color: #48b774;
    padding: 9px 23px;
    text-transform: none;
    line-height: 24px;
    border-radius: 20px;
  }
}

.page-nav__toggle {
  position: absolute;
  top: 39%;
  right: 10px;
  transform: translate(0, -50%);
  display: block;
  margin: 0;
  padding: 0;
  width: 43px;
  height: 42px;
  border: none;
  cursor: pointer;
  background-color: transparent;
  z-index: 2;
}
.page-nav__toggle:hover {
  opacity: 0.6;
}
.page-nav__toggle:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .page-nav__toggle {
    display: none;
  }
}

.page-nav--closed .page-nav__toggle::before {
  content: "";
  position: absolute;
  top: 10px;
  right: 10px;
  width: 23px;
  height: 4px;
  background-color: #ff8345;
  box-shadow: 0 9px 0 0 #ff8345, 0 19px 0 0 #ff8345;
}

.page-nav--opened .page-nav__toggle::before, .page-nav--opened .page-nav__toggle::after {
  content: "";
  position: absolute;
  top: 18px;
  right: 7px;
  width: 28px;
  height: 4px;
  background-color: #ffffff;
  z-index: 1;
}
.page-nav--opened .page-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.page-nav--opened .page-nav__toggle::after {
  transform: rotate(-45deg);
}
.page-nav--opened .page-nav__toggle:active::before, .page-nav--opened .page-nav__toggle:active::after {
  opacity: 0.3;
}

.page-nav--opened .page-nav__wrapper {
  display: block;
  z-index: 1;
}
@media (min-width: 768px) {
  .page-nav--opened .page-nav__wrapper {
    display: grid;
    position: relative;
  }
}

.page-nav--nojs .page-nav__toggle {
  display: none;
}

.page-nav--nojs .page-nav__wrapper {
  display: block;
  position: absolute;
  z-index: 1;
  top: 0;
  width: 100%;
}
@media (min-width: 768px) {
  .page-nav--nojs .page-nav__wrapper {
    display: flex;
    position: relative;
  }
}

@media (min-width: 768px) {
  .site-list {
    display: flex;
    flex-wrap: wrap;
    gap: 22px;
    margin: 0 auto;
    padding: 0 22px;
  }
}
@media (min-width: 1400px) {
  .site-list {
    gap: 45px;
    margin: 0 241px 0 265px;
    padding: 0;
  }
}

.site-list__link {
  display: block;
  padding: 20px;
  font-weight: 400;
  line-height: 24px;
  color: #ffffff;
  text-transform: uppercase;
  border-bottom: 2px solid #ffffff;
  background-color: #a2a2a2;
}
.site-list__link:hover {
  background-color: #48b774;
}
.site-list__link:active {
  background-color: #ff8345;
}
@media (min-width: 768px) {
  .site-list__link {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column-reverse;
    padding: 0;
    color: #4a4a4a;
    text-transform: none;
    border-bottom: none;
    background-color: transparent;
  }
  .site-list__link::before {
    content: "";
    width: 20px;
    height: 2px;
    margin: auto;
    background-color: transparent;
  }
  .site-list__link:hover {
    color: #48b774;
    background-color: transparent;
  }
  .site-list__link:hover::before {
    background-color: #48b774;
  }
  .site-list__link:active {
    color: #ff8345;
  }
  .site-list__link:active::before {
    background-color: #ff8345;
  }
}

.page-main {
  flex-grow: 1;
  position: relative;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .page-main {
    margin: 0;
    padding: 0 60px;
  }
}
@media (min-width: 1400px) {
  .page-main {
    padding: 0;
  }
}

@media (min-width: 1400px) {
  .page-main__wrapper {
    width: 597px;
  }
}

.promo {
  padding: 0 20px;
}
@media (min-width: 768px) {
  .promo {
    padding: 0;
  }
}

.promo__title {
  margin-bottom: 22px;
  font-size: 37px;
  font-weight: 700;
  line-height: 65px;
  color: #2d2d2d;
}
@media (min-width: 768px) {
  .promo__title {
    padding: 0;
    font-size: 48px;
    text-align: center;
  }
}
@media (min-width: 1400px) {
  .promo__title {
    margin-bottom: 10px;
    text-align: left;
  }
}

.promo__title-fragment {
  color: #ff8345;
}

.promo__description {
  margin-bottom: 30px;
  min-height: 78px;
  font-weight: 500;
  font-size: 15px;
  line-height: 26px;
  color: #7c7c7c;
}
@media (min-width: 768px) {
  .promo__description {
    width: 68.6%;
    min-height: min-content;
    text-align: center;
    margin: 0 auto 30px;
  }
}
@media (min-width: 1400px) {
  .promo__description {
    width: 444px;
    text-align: left;
    margin: 0 0 30px;
  }
}

.promo__link {
  font-weight: 500;
  color: #ffffff;
  background-color: #48b774;
  display: block;
  max-width: 126px;
  margin-bottom: 50px;
  padding: 12px 20px;
  font-size: 15px;
  line-height: 22px;
  border-radius: 50px;
  box-sizing: border-box;
}
.promo__link:hover {
  background-color: #48b774;
}
.promo__link:active {
  background-color: #ff8345;
}
@media (min-width: 768px) {
  .promo__link:hover {
    background-color: #48b774;
    opacity: 0.8;
  }
  .promo__link:active {
    background-color: #ff8345;
    opacity: 1;
  }
}
@media (min-width: 768px) {
  .promo__link {
    text-align: center;
    margin: 0 auto 50px;
  }
}
@media (min-width: 1400px) {
  .promo__link {
    text-align: center;
    margin: 0 0 50px;
  }
}

.form {
  margin-bottom: 40px;
}

.form__search {
  display: flex;
  align-items: center;
  position: relative;
}

.form__input {
  padding: 20px 77px 22px 30px;
  width: 100%;
  min-height: 62px;
  font-family: "Roboto", "Arial", "Helvetica", sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #a2a2a2;
  letter-spacing: 0.01em;
  background: #ffffff;
  box-shadow: 0 20px 30px rgba(222, 222, 222, 0.25);
  border-radius: 100px;
  border-style: none;
  box-sizing: border-box;
}
@media (min-width: 1400px) {
  .form__input {
    padding: 21px 70px 22px 40px;
    font-size: inherit;
  }
}

.form__search-icon {
  position: absolute;
  right: 21px;
  z-index: 2;
}

.form__button {
  font-weight: 500;
  color: #ffffff;
  background-color: #48b774;
  display: block;
  position: absolute;
  right: -14px;
  width: 57px;
  height: 57px;
  margin: 20px;
  border: none;
  border-radius: 50px;
  color: transparent;
}
.form__button:hover {
  background-color: #48b774;
}
.form__button:active {
  background-color: #ff8345;
}
@media (min-width: 768px) {
  .form__button:hover {
    background-color: #48b774;
    opacity: 0.8;
  }
  .form__button:active {
    background-color: #ff8345;
    opacity: 1;
  }
}
.form__button::after {
  content: "";
  display: block;
  position: absolute;
  margin: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 27px;
  height: 27px;
  background: url("../img/icon-search.svg") no-repeat;
}

.info {
  display: flex;
  gap: 12px;
  align-items: center;
  padding: 0 20px;
  margin-bottom: 50px;
}
@media (min-width: 768px) {
  .info {
    justify-content: flex-start;
    padding: 0;
  }
}
@media (min-width: 1400px) {
  .info {
    margin-bottom: 0;
  }
}

@media (min-width: 768px) {
  .info__information {
    display: flex;
  }
}

.info__text,
.info__link-price {
  font-family: "Roboto", "Arial", "Helvetica", sans-serif;
  line-height: 19px;
  color: #707070;
}

.info__link-price {
  padding-left: 3px;
  font-weight: 500;
  color: #fd905a;
}

.card-profile {
  display: flex;
  flex-wrap: wrap;
}

.card-profile__item:not(:first-child) {
  margin-left: -17px;
}

.card-profile__item:first-child {
  position: relative;
  z-index: 1;
}

.card-profile__item:nth-child(3) {
  position: relative;
  z-index: -1;
}

.card-profile__item:nth-child(4) {
  position: relative;
  z-index: -2;
}

.carousel {
  display: none;
}
@media (min-width: 768px) {
  .carousel {
    display: block;
    position: relative;
  }
}
@media (min-width: 1400px) {
  .carousel {
    position: absolute;
    top: -39px;
    right: -12px;
  }
}

.carousel__image {
  position: absolute;
  top: 45%;
  left: 50%;
  right: 0;
  bottom: 0;
  margin: auto;
  display: flex;
  object-fit: cover;
  width: auto;
  transform: translate(-50%);
}

.icon-transition1,
.icon-transition2,
.icon-transition3,
.icon-transition4,
.icon-transition5 {
  position: absolute;
  display: flex;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
}

.icon-transition1 {
  width: 100px;
  animation: icon-transition1 0.4s linear forwards;
}

.icon-transition2 {
  width: 150px;
  animation: icon-transition2 0.4s linear 0.4s forwards;
}

.icon-transition3 {
  width: 125px;
  animation: icon-transition3 0.4s linear 0.8s forwards;
}

.icon-transition4 {
  width: 125px;
  animation: icon-transition4 0.4s linear 1.6s forwards;
}

.icon-transition5 {
  width: 100px;
  animation: icon-transition5 0.4s linear 2s forwards;
}

@keyframes icon-transition1 {
  from {
    transform: translate(0, 0);
  }
  to {
    transform: translate(-220px, 60px);
  }
}
@keyframes icon-transition2 {
  from {
    transform: translate(0, 0);
  }
  to {
    transform: translate(-196px, -93px);
  }
}
@keyframes icon-transition3 {
  from {
    transform: translate(0, 0);
  }
  to {
    transform: translate(20px, -212px);
  }
}
@keyframes icon-transition4 {
  from {
    transform: translate(0, 0);
  }
  to {
    transform: translate(194px, -90px);
  }
}
@keyframes icon-transition5 {
  from {
    transform: translate(0, 0);
  }
  to {
    transform: translate(220px, 60px);
  }
}
.icon-transition1-1 {
  transform: rotate(-195deg) translate(228px, -1px) rotate(195deg);
  animation: icon-transition1-1 10s linear infinite;
}

.icon-transition2-1 {
  transform: rotate(-155deg) translate(217px, 2px) rotate(155deg);
  animation: icon-transition2-1 10s linear infinite;
}

.icon-transition3-1 {
  transform: rotate(-90deg) translate(212px, 20px) rotate(90deg);
  animation: icon-transition3-1 10s linear infinite;
}

.icon-transition4-1 {
  transform: rotate(336deg) translate(214px, -3px) rotate(-336deg);
  animation: icon-transition4-1 10s linear infinite;
}

.icon-transition5-1 {
  transform: rotate(15deg) translate(227px, 1px) rotate(-15deg);
  animation: icon-transition5-1 10s linear infinite;
}

@keyframes icon-transition1-1 {
  from {
    transform: rotate(-195deg) translate(228px, -1px) rotate(195deg);
  }
  to {
    transform: rotate(165deg) translate(228px, -1px) rotate(-165deg);
  }
}
@keyframes icon-transition2-1 {
  from {
    transform: rotate(-155deg) translate(217px, 2px) rotate(155deg);
  }
  to {
    transform: rotate(205deg) translate(217px, 2px) rotate(-205deg);
  }
}
@keyframes icon-transition3-1 {
  from {
    transform: rotate(-90deg) translate(210px, 18px) rotate(90deg);
  }
  to {
    transform: rotate(270deg) translate(210px, 18px) rotate(-270deg);
  }
}
@keyframes icon-transition4-1 {
  from {
    transform: rotate(336deg) translate(214px, -3px) rotate(-336deg);
  }
  to {
    transform: rotate(696deg) translate(214px, -3px) rotate(-696deg);
  }
}
@keyframes icon-transition5-1 {
  from {
    transform: rotate(15deg) translate(227px, 1px) rotate(-15deg);
  }
  to {
    transform: rotate(375deg) translate(227px, 1px) rotate(-375deg);
  }
}
.carousel-list {
  width: 450px;
  height: 450px;
  border: 2px solid #cccccc;
  border-radius: 50%;
  box-sizing: border-box;
  position: relative;
  margin: 0 auto;
}
.carousel-list::after {
  content: "";
  position: absolute;
  top: 70px;
  left: 5px;
  right: 0;
  bottom: 0;
  margin: auto;
  display: flex;
  width: 280px;
  height: 280px;
  border-radius: 50%;
  background-color: #48b774;
}